import mui.material.List as MaterialList

val Sidebar = FC<Props> {
    val app = useContext(ApplicationContext)!!
    val lastUpdate = app.summary?.lastUpdate?.toDate()

    Drawer {
        variant = DrawerVariant.temporary
        open = app.sidebarOpened
        // anchor = DrawerAnchor.left
        anchor = DrawerAnchor.right
        onBackdropClick = { app.setSidebarOpened(false) }

        Stack {
            sx = jso {
                height = 100.vh
                width = 100.vw
                maxWidth = 250.px
            }

            //spacing = responsive(1.em)
            direction = responsive(StackDirection.column)
            divider = Divider.create()

            Box {
                sx = jso {
                    flexGrow = number(0.0)
                    flexShrink = number(0.0)
                    textAlign = TextAlign.right
                }

                IconButton {
                    onClick = { app.setSidebarOpened(false) }
                    size = Size.large
                    Close()
                }
            }

            MaterialList {
                sx = jso {
                    flexGrow = number(1.0)
                    paddingTop = 0.px
                    paddingBottom = 0.px
                }

                SidebarLink {
                    title = "Kalender"
                    target = "/"
                    icon = Schedule
                }

                SidebarLink {
                    title = "Wer zeigt was?"
                    target = "/broadcaster"
                    icon = LiveTv
                }

                SidebarLink {
                    title = "Bleib' up to date!"
                    target = "/subscribe"
                    icon = Subscriptions
                }
            }

            if (app.canInstall()) {
                SidebarLink {
                    title = "App installieren"
                    icon = InstallMobile
                    onClick = {
                        app.doInstall()
                    }
                }
            }

            Box {
                sx = jso {
                    paddingTop = 0.75.rem
                    paddingBottom = 0.75.rem
                    paddingLeft = 0.5.rem
                    paddingRight = 0.5.rem
                    textAlign = TextAlign.center
                }

                Typography {
                    component = ReactHTML.div
                    variant = TypographyVariant.caption
                    +"Deine Zeitzone:"
                }

                Typography {
                    sx = jso {
                        lineHeight = number(1.0)
                    }
                    component = ReactHTML.div
                    variant = TypographyVariant.overline
                    +TIMEZONE
                }

                if (lastUpdate != null) {
                    Typography {
                        sx = jso {
                            marginTop = 0.75.rem
                        }
                        component = ReactHTML.div
                        variant = TypographyVariant.caption
                        +"Letztes Update:"
                    }

                    Typography {
                        sx = jso {
                            lineHeight = number(1.0)
                        }
                        component = ReactHTML.div
                        variant = TypographyVariant.overline
                        +lastUpdate.formatAsDateTime(false)
                    }
                }
            }

            Stack {
                sx = jso {
                    padding = 0.5.rem
                    justifyContent = JustifyContent.spaceAround
                }

                spacing = responsive(1.rem)
                direction = responsive(StackDirection.row)

                Tooltip {
                    title = ReactNode("Kampfkalender bei Twitter")
                    IconButton {
                        onClick = { window.open(TWITTER_URL, "_blank") }
                        Twitter {
                            fontSize = SvgIconSize.large
                        }
                    }
                }

                //Tooltip {
