                //            }
                //        }
                //    }
                //}

                Tooltip {
                    title = ReactNode("Kampfkalender bei GitHub")
                    IconButton {
                        onClick = { window.open(GITHUB_URL, "_blank") }
                        GitHub {
                            fontSize = SvgIconSize.large
                        }
                    }
                }
            }
        }
    }
}
